import React, { useState } from 'react'
import { connect, useDispatch } from 'react-redux';
import { Link,useNavigate } from 'react-router-dom'
import { loadingToggleAction,loginAction,
} from '../../store/actions/AuthActions';
import { FaEye, FaEyeSlash } from "react-icons/fa"; // Add this import

// image
// import logo from "../../images/logo-veezzo.png";
import logo from "../../jsx/logo-veezzo-login.png";
import loginbg from "../../images/pic1.png";

function ShopVendorLogin (props) {
  const [email, setEmail] = useState('');
    let errorsObj = { email: '', password: '' };
    const [errors, setErrors] = useState(errorsObj);
    const [password, setPassword] = useState('');
const [showPassword, setShowPassword] = useState(false); // Add this state
    const navigate = useNavigate();
    const dispatch = useDispatch();
    
    function onLogin(e) {
        e.preventDefault();
        let error = false;
        const errorObj = { ...errorsObj };
        if (email === '') {
            errorObj.email = 'Email is Required';
            error = true;
        }
        if (password === '') {
            errorObj.password = 'Password is Required';
            error = true;
        }
        setErrors(errorObj);
        if (error) {
            return ;
        }
        dispatch(loadingToggleAction(true));	
        dispatch(loginAction(email, password, 'vendor', navigate));    }

  return (
        <div className="authincation-shopvendor d-flex flex-column flex-lg-row flex-column-fluid">
            <div className="login-aside text-center  d-flex flex-column flex-row-auto">
                <div className="d-flex flex-column-auto flex-column pt-lg-40 pt-15">
                  	<div className="text-center pt-5">
  <h3 className="mb-1">Shop Vendor Login!</h3>

	<img
 src="/logo-veezzo-login.png"  alt="Logo"
  style={{
    width: '300px',
    height: 'auto',
    marginTop: '30px',
    marginBottom: '20px',
  }}
/>
  <h3 className="mb-2">Welcome back!</h3>
  <p>Veezzo â€“ Everything Fresh, Every Day</p>
</div>
</div>
                {/* <div className="aside-image" style={{backgroundImage:"url(" + loginbg + ")"}}></div> */}
            </div>
            <div className="container flex-row-fluid d-flex flex-column justify-content-center position-relative overflow-hidden p-7 mx-auto">
                <div className="d-flex justify-content-center h-100 align-items-center">
                    <div className="authincation-content style-2">
                        <div className="row no-gutters">
                            <div className="col-xl-12 tab-content">
                                <div id="sign-in" className="auth-form   form-validation">
                                    {props.errorMessage && (
                                        <div className='bg-red-300 text-red-900 border border-red-900 p-1 my-2'>
                                            {props.errorMessage}
                                        </div>
                                    )}
                                    {props.successMessage && (
                                        <div className='bg-green-300 text-green-900 border border-green-900 p-1 my-2'>
                                            {props.successMessage}
                                        </div>
                                    )}
                                    <form onSubmit={onLogin}  className="form-validate">
                                        <h3 className="text-center mb-4 text-black">Sign in your account</h3>
                                        <div className="form-group mb-3">
                                            <label className="mb-1"  htmlFor="val-email"><strong>Email</strong></label>
                                            <div>
                                                <input type="email" className="form-control"
                                                    value={email}
                                                   onChange={(e) => setEmail(e.target.value)}
                                                   placeholder="Type Your Email Address"
                                                />
                                            </div>
                                            {errors.email && <div className="text-danger fs-12">{errors.email}</div>}
                                        </div>
                                        <div className="form-group mb-4">
      <label className="mb-1"><strong>Password</strong></label>
      <div style={{ position: "relative" }}>
        <input
          type={showPassword ? "text" : "password"}
          className="form-control"
          value={password}
          placeholder="Type Your Password"
          onChange={(e) => setPassword(e.target.value)}
        />
        <span
          onClick={() => setShowPassword((prev) => !prev)}
          style={{
            position: "absolute",
            right: 12,
            top: "50%",
            transform: "translateY(-50%)",
            cursor: "pointer",
            color: "#888"
          }}
        >
          {showPassword ? <FaEyeSlash /> : <FaEye />}
        </span>
      </div>
        {errors.password && <div className="text-danger fs-12">{errors.password}</div>}
    </div>
                                        {/* <div className="form-row d-flex justify-content-between mt-4 mb-2">
                                            <div className="form-group mb-3">
                                               <div className="custom-control custom-checkbox ml-1">
                                                    <input type="checkbox" className="form-check-input" id="basic_checkbox_1" />
                                                    <label className="form-check-label" htmlFor="basic_checkbox_1">Remember my preference</label>
                                                </div>
                                            </div>
                                        </div> */}
                                        <div className="text-center form-group mb-3">
                                            <button type="submit" className="btn btn-secondary btn-block">
                                                Sign In
                                            </button>
                                        </div>
                                    </form>
                                    {/* <div className="new-account mt-3">
                                        <p>Don't have an account? <Link className="text-secondary" to="/page-register">Sign up</Link></p>
                                    </div> */}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>	
        
  );
};

const mapStateToProps = (state) => {
    return {
        errorMessage: state.auth.errorMessage,
        successMessage: state.auth.successMessage,
        showLoading: state.auth.showLoading,
    };
};
export default connect(mapStateToProps)(ShopVendorLogin);
